

























































/* make sidebar nav vertical */
@media (min-width: 768px) {
.sidebar-nav .side-menu ul[data-v-7d5a00c9] {
    float: none;
    margin-top:40px;
}
.sidebar-nav .side-menu ul[data-v-7d5a00c9]:not {
    display: block;
}
.sidebar-nav .side-menu li[data-v-7d5a00c9] {
    float: none;
    display: block;
}
.sidebar-nav .side-menu li i[data-v-7d5a00c9] {
    padding-top: 12px;
    padding-bottom: 12px;
    margin: 0 auto;
    font-size: 45px;
    -webkit-transition: 2s linear all;
    transition: 2s linear all;
}
}
 
/* Making sidenav height 100% */
.sidebar-nav[data-v-7d5a00c9] {
  height: 100vh;
  position: absolute;
  left: 0;
}
.menu-content[data-v-7d5a00c9] {
  height: 100%;
}
.menu-content li[data-v-7d5a00c9] {
  cursor: pointer;
  width: 100%;
  -webkit-transition: 3s linear all;
  transition: 3s linear all;
}
.menu-content li span.first-item[data-v-7d5a00c9]:hover {
  background-color: #FB4D00;
}
.menu-content li span.second-item[data-v-7d5a00c9]:hover {
  background-color: #FF003E;
}
.menu-content li span.third-item[data-v-7d5a00c9]:hover {
  background-color: #FF008B;
}
.menu-content li span.fourth-item[data-v-7d5a00c9]:hover {
  background-color: #AA07EB;
}
.menu-content li span.fifth-item[data-v-7d5a00c9]:hover {
  background-color: #6C0BFF;
}
.side-menu[data-v-7d5a00c9] {
  height: 100vh;
  background-color: transparent;
  border: none;
  position: fixed;
  width: 5vw;
  overflow: hidden;
  transition: 3s linear all;
  -webkit-transition: 3s linear all;
  z-index: 99999;
}
.side-menu.side-menu-opened[data-v-7d5a00c9] {
  width: 100vw;
  background-color:rgba(0,0,0,0.9);
  transition: 3s linear all;
  -webkit-transition: 3s linear all;
}
.side-menu:hover span[data-v-7d5a00c9] {
  display: block;
}
.portfolio-logo[data-v-7d5a00c9] {
  margin-top: 10px;
}


/* Icons */
.menu-item span[data-v-7d5a00c9] {
  text-align: center;
  -webkit-animation: fadein 5s;
  animation: fadein 5s;
  line-height: 88px;
  font-size: 30px;
  margin: 0 auto;
  display: none;
  color: #FFF;
}


/* Animations */
@keyframes fadein {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@-webkit-keyframes fadein {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}


/** Ninja */
.ninja[data-v-7d5a00c9] {
  width: 70px;
  height: 55px;
  background: url('../assets/images/standing-ninja_70x55.png');
  -webkit-transition: 20s linear left;
  transition: 20s linear left;
  float: right;
  margin-top: 50vh;
  opacity:0;
}
.idle .ninja[data-v-7d5a00c9] {
  opacity: 1 !important;
}
.side-menu:hover .ninja[data-v-7d5a00c9] {
  background: url('../assets/images/ninja-running-red-1.png');
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  -webkit-animation: play .8s steps(6) infinite;
  animation: play .8s steps(6) infinite;
}
@-webkit-keyframes play {
from {
    background-position: 0px;
}
to {
    background-position: -420px;
}
}
@keyframes play {
from {
    background-position: 0px;
}
to {
    background-position: -420px;
}
}




