
.intro-container {
    height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#Intro {
    width: 100%;
    color: #FFF;
    font-size: 35px;
    text-align: center;
    margin: 0 auto;
    font-size: 35px;
}
.welcome {
    text-align: center;
    -webkit-animation: fadein 10s;
    animation: fadein 10s;
    color: #FFF;
}
@keyframes fadein {
from {
        opacity: 0;
}
to {
        opacity: 1;
}
}
@-webkit-keyframes fadein {
from {
        opacity: 0;
}
to {
        opacity: 1;
}
}



